/* Strikethrough */

.App_strikethrough__1gs-y {
	display: inline-block;
	position: relative;
  transition: all 0.5s cubic-bezier(.55, 0, .1, 1);
  color: rgba(79, 176, 174, .3);
}

.App_strikethrough__1gs-y::after {
	content: '';
	position: absolute;
	display: block;
	width: 100%;
	height: 2px;
  /* box-shadow: 0 1px rgba(0, 0, 0, 0.6); */
  background-color: rgba(79, 176, 174);
  margin-top: -0.7em;
  transform-origin: center left;
	-webkit-animation: App_strikethrough__1gs-y 1s cubic-bezier(.55, 0, .1, 1) 1;
	        animation: App_strikethrough__1gs-y 1s cubic-bezier(.55, 0, .1, 1) 1;
}

/* Keyframes for initial animation */

@-webkit-keyframes App_strikethrough__1gs-y {
	from {
		transform: scaleX(0);
	}
	to {
		transform: scaleX(1);
	}
}

@keyframes App_strikethrough__1gs-y {
	from {
		transform: scaleX(0);
	}
	to {
		transform: scaleX(1);
	}
}

